

//BOUNCE K CITÁTŮM
//



.car-tip {
  width:100%;


}




.card-columns {
  box-shadow:  -7px 4px 34px 0px rgba(0,0,0,0.75);
  -webkit-box-shadow: -7px 4px 34px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: -7px 4px 34px 0px rgba(0,0,0,0.75);
}

.arrow {
  max-width: 70%;
  opacity: 0.4;
}

.box-container {
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:distribute;
  justify-content:space-around;
  padding-top:0.6em;

}

.box-quote {
     display: block;
    width: 31.5%;
    height: auto;

}


.card {
  padding-bottom:2em;
}


.bounce-left,
.bounce-right {
  margin:0em;
}


iframe {
  width: 100%;
  height: 14em;
}

.quotes {
  margin:0em;
}

.quotes p {
  margin:0em;
  line-height: 1em;
}




/*animation element*/

.animation-element {
  position: relative;
  width: 30%;
  margin: 0% 1.33 2.66% 1.33%;
  float: left;
  overflow: hidden;
}
/*3 grid layout*/

.animation-element:nth-of-type(3n-2) {
  width: 31.5%;
  margin: 0% 1.375% 2.75% 0%;
  clear: left;
}

.animation-element:nth-of-type(3n-1) {
  width: 31.5%;
  margin: 0% 1.375% 2.75% 1.375%;
}

.animation-element:nth-of-type(3n-0) {
  width: 31.5%;
  margin: 0% 0% 2.75% 1.375%;
  clear: right;
}
/*bounce up animation for the subject*/

.bounce-left .quotes {
  opacity: 0;
  -moz-transition: all 700ms ease-out;
  -webkit-transition: all 700ms ease-out;
  -o-transition: all 700ms ease-out;

  transition: all 700ms ease-out;
  -moz-transform: translate3d(50px, 0px, 0px);
  -webkit-transform: translate3d(50px, 0px, 0px);
  -o-transform: translate(50px, 0px);
  -ms-transform: translate(50px, 0px);

  transform: translate3d(50px, 0, 0px);
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}

.bounce-left.in-view .quotes {
  opacity: 1;
  -moz-transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  
  transform: translate3d(0px, 0px, 0px);
}

.quotes {
  float: left;
  color: black;
  width: 100%;
  margin: 0% 1.33% 2.66% 1.33%;
  background: #fff;
  padding: 15px;
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.2);
  border: none;
}



.bounce-right .quotes {
  opacity: 0;
  -moz-transition: all 700ms ease-out;
  -webkit-transition: all 700ms ease-out;
  -o-transition: all 700ms ease-out;

  transition: all 700ms ease-out;
  -moz-transform: translate3d(-50px, 0px, 0px);
  -webkit-transform: translate3d(-50px, 0px, 0px);
  -o-transform: translate(-50px, 0px);
  -ms-transform: translate(-50px, 0px);

  transform: translate3d(-50px, 0, 0px);
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
}

.bounce-right.in-view .quotes {
  opacity: 1;
  -moz-transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
  -o-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  
  transform: translate3d(0px, 0px, 0px);
}





/*media queries for small devices*/

@media screen and (max-width: 678px) {
  .main-container {
    margin: 20px 15px;
  }
  .animation-element,
  .animation-element:nth-of-type(3n-1),
  .animation-element:nth-of-type(3n-2),
  .animation-element:nth-of-type(3n-0) {
    width: 100%;
    margin: 0px 0px 30px 0px;
  }
  .subject .header .date,
  .subject .header .category {
    width: 100%;
    margin-bottom: 10px;
    text-align: center;
  }
}









