@import 'variables.scss';

.container{

	max-width:1400px;
	margin:0 auto;
	text-align: center;
	background: white;
	
}


.container-car {

	max-width:1100px;
	margin:0 auto;
	background: white;
}

.container-cover-lable {
	max-width:1400px;
	margin:0 auto;
	height:20em;
	
}














//navigation


.topbar {
	display:flex;
	justify-content: space-around;
	height:6em;	
	padding:0.4em;
	border-bottom: 1px dotted grey;
}


.contacts {
	padding:2em 1em;
}


.navigation {
	margin-top:1em;
	margin-bottom:2em;
	display:flex;
	justify-content:center;
}


.call {
	color:red;
	font-size: 1.4em;
	padding-left:0.8em;
}


.nav-link {
	font-size:1.3em;
	font-weight: bold;
	width:6em;
	margin-right:2em;
	padding:0.2em;
	text-transform: uppercase;
	border-left: 1px solid red;
	border-right: 1px solid red;
}











//cover


.cover-photo{

	width:100%;
	height: 30em;
	background:url("https://cdn.pixabay.com/photo/2015/01/19/13/51/car-604019_1280.jpg") no-repeat;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.label {
	height:20em;
	width:100%;
	margin: 5em 0.2em;
	border-radius: 10em;
	line-height: 1.8em;
	font-family: roboto,arial,sans-serif;

}

.cover-wrapper {
	display:flex;
	justify-content: space-between;
}


.title-text {
	border-radius: 4em;
    display: block;
    background: #ffffffbf;
    padding: 2em;
    width: 95%;
    height: 88%;
    margin: 1em;
    & h1 {
    	margin:0;
    	padding:0 0 0.7em 0;
    
    }
    & .button {
    margin-top: 1em;
    }
}

ul {
	text-decoration: none;
	margin:0;
	padding:0;
}

.fa-check-double {
	padding:0.2em 1em;
}











// find a car


.listing-car {
	& .button {
		width: 100%;
	    margin-top: -1em;
	    font-size: 2em;

	}

}


.find-form {
	display:none;
	position:relative;
	width:100%;
	height:5em;
	padding:2em 1em 1em 1em;
	border:1px dotted red;
}

select {
	width:10em;
	border: 1px dotted red;
	margin-right:1em;
}







//news
.news {
	padding:1em;
	margin:0 auto;
	width:90%;
	display:flex;
	justify-content:space-between;
	& h2 {
		font-size:4em;
		color:$offer-color;
		margin: 0em;
	}

}


.fa-car {
	font-size:4em;
	color:$offer-color;
	padding-top:1em;
}




.new-counter {
	flex:1;
}

.news-text {
	flex: 1;
	text-align: left;
	padding-top:2em;
	font-size:1.6em;
}













//tip car


.tip-inf, .tip-inf-not {
	padding-right: 1em;
	padding-left: 1em;
	
}


.tip-inf {

		border-left: 1px dotted red;

}




//offer, about us
.offer {
	display:flex;
	justify-content:space-between;
	padding:1em;
}


.about-us{
	font-size: 1.4em;
	color:$offer-color;
	padding:1em 3em 3em 3em;
}


.fa-funnel-dollar,
.fa-hand-holding,
.fa-file-alt,
.fa-question {
	padding:0.6em;
	font-size:3em;
	color:$offer-color;
}
















//button

.button {  
    cursor: pointer;
   	margin:0 auto;
    background-color: $button-background;
    display: block;
    border-radius: 5px;
    border: 1px solid $general-color;
    width: 8.43em;
    padding: 8px 0;
    text-align: center;
    color: $general-color;
    text-decoration: none;
    transition: 0.4s;
    &:hover {
    	background-color:$button-background-hover;
    	color:$button-link-hover;
   

    }
}







//car-box


.car-identiti {
	max-width:100%;
	margin:0 auto;
}

.car-title {
	font-size:2em;
	padding:0.2em 0em;
	color:$general-color;

}



.car-box {
	padding: 1em 0em;
	display:flex;
	justify-content:center;
	
}

.car-box-photos {
	flex: 2;
	margin: 0em 1em 1em 0em;
}

.car-thumb-photo {
	max-width: 20%;
	padding: 0.2em 0em;

}

.car-main-photo {
	max-width: 100%;

}

.car-info-price {
	display:flex;
	justify-content: space-between;
	border-bottom: 1px dotted grey;
}

.price {
	color:$general-color;
	font-size:2em;
	padding:0.4em;
	font-weight: bold;
}


.tax {
	color:$font-color;
	font-size:1.2em;
	padding: 1.4em 1em;
}


.car-info-line {
	display:block;
	padding:1em;
	max-width: 100%;
}

.car-details {
	padding:2em;
}


